<template lang='pug'>
div
  div.row
    div.col-xs-12.text-center
      h3 Unmatched
  div.row
    div.col-xs-2
      h4 Men:
    div.col-xs-10
      SM-person-box(
        v-for='index in unmatched.men'
        :gender='"m"'
        :index='index'
        :key='index'
      )
  div.row
    div.col-xs-2
      h4 Women:
    div.col-xs-10
      SM-person-box(
        v-for='index in unmatched.women' 
        :gender='"w"'
        :index='index'
        :key='index'
      )
</template>

<script>
import SMPersonBox from './SMPersonBox'
export default {
  components: {
    SMPersonBox
  },
  props: [
    'n',
    'colors',
    'unmatched'
  ],
  // end props
  data () {
    return {

    }
  }
}
</script>

<style scoped>
h4 {
  padding-top: 8px;
  text-align-last: right;
}
div.row {
  margin-bottom: 5px;
}
</style>


